

@media screen and (max-width:900px) {
    
    
    nav{
         display: flex;
        justify-content: center;
        flex-direction: column;
        h1{
            width: 100%;
            text-align: center;
        }
        main{
            width: 100%;
            justify-content:center;
            a{
                margin: 0.5rem;
            }
        }
    }
    
    
    
    .home3{
        padding: 2rem;
        height: fit-content;
        >div{
            width: 100%;
            border-radius: 20px 20px 20px 20px;
            padding: 2rem;
            height: fit-content;
        }
    }
    .home{
        main{
            padding: 3rem;
            border-radius: 0 0 5px 5px;
            h1{
                font-size: 24 !important;
                text-transform: capitalize !important;

            }
        }
        &::before{
            border-radius: 0 0 5px 5px;
        }
    }
    .home4{
        height: fit-content;
        padding: 2rem;
        margin-top: -2px;
        >div{
            width: 100%;
         border-radius: 10px 10px 10px 10px;
        height: fit-content;    
        }
        article{
            margin: 50px 10px;
        }
    }

    .contact{
        height: fit-content;
        padding: 1rem;
        main{
            width: 100%;
            border-radius: 20px 20px 20px 20px;
            margin: 10px;
            form{
                margin: 20px;
                div{
                    width:100%;
                    display: contents;
                    label{
                        width: 100%;margin: 20px;
                    }
                    input{
                        width: 100%;
                    }
                }
            }
        }
    }
    
}
@media screen and (max-width: 600px) {
    .home2{
        flex-direction: column;
        padding: 570px 10px;
        img{
            width: 100%;
        }
        div{
            width: 100%;
        }
    }
  }