@import "colors";

nav{
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background-color: $first;
    z-index: 10;
    top: 0%;
    h1{
     text-transform: uppercase;   
    }
    main{
        width: 70%;
        display: flex;
        justify-content: flex-end;

        a{
            color:$dark;
            margin: 1rem;
            
            &:hover{
                color:$third;
            }

        }
    }
}