@import "colors.scss";

.home{
    width: 100%;
    height:100vh;
    background-color: $second;
    border-radius: 0 0 200px 200px;
    main{
        position: absolute;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        width: 100%;
        height: 100%;
        padding: 2rem;
        justify-content: center;
        h1{
            font:400  $text1;
            font-size: 4rem;
            color:$first;
        }
        p{
            font:900 1 rem $text1;
            color:$first;
        }

    }

    &::before{
        content:"";
        width: 100%;
        height:100%;
        position: absolute;
        background: no-repeat center/auto url("../assets/png1.png");
        border-radius: 0 0 200px 200px;
      
    }

}
.home2{
    width:100%;
    height: 100vh;
    background-color: $second;
    padding: 500px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -200px;
    >img{
        width: 30%;
        animation :imgUp 0.7s linear infinite alternate;
    }
    div{
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 4%;
        height: 100vh;
        p{
            color: $first;
            letter-spacing: 2px;
            word-spacing: 5px;
            font :100 1.2rem $text2;

        }
    }

}
@keyframes imgUp {
    to {
        transform:translateY(-20px);
    }
}


.home3{
    width:100%;
    height: 120vh;
    background-color: $second;
    margin-top: -200px;
    display: flex;
    div{
        width: 70%;
        height: 100vh;
        background-color: $first;
        border-radius: 0 200px 200px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding:4%;
        h2{
            text-transform: uppercase;
            border-bottom: 2px solid $dark;
            text-align: center;
            padding: 1%;
            width: 15rem;
            color: $dark;
        }

        p{
            padding: 2%;
            letter-spacing: 1px;
            word-spacing: 5px;
            line-height: 170%;
            font-style: italic;
            font:italic 100 1.2 rem $text2;

        }

    }
}
.home4{
    width:100%;
    height: 100vh;
    background-color: $second;
    display: flex;
    justify-content: flex-end;

    >div{
        background-color: $third;
        border-radius: 200px 0 0 200px;
        width: 70%;
        height: 70vh;
        padding: 2%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    h2{
        color: $first;
        text-transform: uppercase;
        width: 140px;
        border-bottom: 2px solid $first;
        padding: 1%;
        text-align: center;
    }
    article{
        display: flex;
        margin :2% 0;
        flex-wrap: wrap;
        justify-content: center;
        width: 80%;

        >div{
            animation: imgUp 1s linear infinite alternate;
            margin: 1%;
            height: 10rem;
            width: 10rem;
            background-color: $first;
            border-radius: 50%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: $dark;
            cursor: pointer;
            transition: all 0.5s;
            &:hover{
                filter: invert(1);
            }
            svg{
                font-size: 3rem;
            }
            p{
              font: 900 1rem $text2;  
            }

        }
    }
}